$('.card-item-body').hide();// 
$('.card-item').eq(0).addClass('active'); // 
$('.card-item-body').eq(0).show();// 

$('.card-item').click(function (e){
  e.preventDefault();
  $(this).toggleClass('active').siblings().removeClass('active');//
  $(this).children().next().slideToggle();
  $(this).siblings().children().next().slideUp();
})